import React from 'react';

import { 
    ContainerGaleria,
    Card,
} from './styles';

import { Carousel } from '../../components/Carousel';

const Galeria = ({account}) => {

    const [carousel, setCarousel] = React.useState({ status: false, index: 0});

    const setIndexCarousel = (index) => {
      setCarousel({ status: true, index: index})
    }

    React.useEffect(()=>{
        console.log(account.user.galeria.imagens)
    },[])

    return (
        <ContainerGaleria>
                {
                    account.user.galeria.imagens.map((item, index) => (
                        <Card key={index} >
                                <img src={`${item?.img}`} alt={item.nome}
                                    style={{
                                        cursor: 'pointer'
                                    }}  
                                    onClick={() => { 
                                        setCarousel({ status: true, index: index}) 
                                    }}
                                />
                        </Card>
                    )) 
                }
        <Carousel carousel={carousel} close={ () => setCarousel({ status: false, index: 0}) } imagesList={ account.user.galeria?.imagens ? account.user.galeria.imagens : []} setIndexCarousel={setIndexCarousel} />
        </ContainerGaleria>
    )

}

export  { Galeria };