import * as React from 'react';


import { 
  Container, 
  Main,
} from '../../styles/Layout';

import {
        WrapperCartao,
        Logo,
        WrapperPerfil,
        WrapperAvatar,
        WrapperPerfilInfo,

        WrapperPerfilIdentidade,
        WrapperAvatarIdentidade,
        WrapperPerfilInfoIdentidade,
        useStyles,
  } from './styles';

import Avatar from '@material-ui/core/Avatar';


import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';


import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';

import EmailIcon from '@material-ui/icons/Email';
import PhoneIcon from '@material-ui/icons/Phone';
import RoomIcon from '@material-ui/icons/Room';

import { useGenericModel } from '../../hooks';
import { HeadContext } from '../../contexts';

import { TabServico } from './Servico';
import { TabBio } from './Bio';
import { Galeria } from './Galeria';
import { TabComponet } from '../../contexts/Tab';

function Home({match, location }) {

  const classes = useStyles();

  const [account, setAccount, AccountInicial] = useGenericModel('account_profile', match?.params.a);
  const { head, setHead } = React.useContext(HeadContext);
  const [tab, setTab] = React.useState(0);

  React.useEffect( () => {
      console.log(account)
      if(account){
        setHead(account)
      }
  },[account])


  return (
    <>
    {
      account?.id ?
        <Container>
        <Main>

      {
        account?.modelo === 0 ?
        <>
          <WrapperCartao color={account.user.cartao?.cor} >
              <Logo src={account.user.cartao?.logo}/> 
          </WrapperCartao>

          <Accordion >
            <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
              <WrapperPerfil >
                    <WrapperAvatar>
                      <Avatar alt="Diego Fontes" src={account.user.profile?.avatar} className={classes.avatar} />
                    </WrapperAvatar>
                    <WrapperPerfilInfo >
                        <Typography variant="h6">
                            {account.user?.first_name}
                        </Typography>
                        <Typography variant="caption" color="textSecondary">
                          {account.user.profile?.profissao}
                        </Typography>
                    </WrapperPerfilInfo>
                </WrapperPerfil>
            </AccordionSummary>
          <AccordionDetails>
                      
            <List>
                <ListItem>
                  <ListItemAvatar>
                      <EmailIcon />
                  </ListItemAvatar>
                  <ListItemText primary="E-mail" secondary={account.user?.email} />
                </ListItem>

                <ListItem>
                  <ListItemAvatar>
                      <PhoneIcon />
                  </ListItemAvatar>
                  <ListItemText primary="Telefone" secondary={account.user.profile?.telefone} />
                </ListItem>

                <ListItem>
                  <ListItemAvatar>
                      <RoomIcon />
                  </ListItemAvatar>
                  <ListItemText primary="Endereço" secondary={account.user.profile?.endereco} />
                </ListItem>

            </List>

          </AccordionDetails>
        </Accordion>
      </>
      :
      account?.modelo === 1
       ?
       <>
          <Accordion 
            expanded={true}
          >
            <AccordionSummary
                // expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
                style={{
                  backgroundImage: `linear-gradient(to bottom, ${ account?.cor_principal ? account?.cor_principal : "#ffffff"} , #ffffff)`,
                }}
              >
              <WrapperPerfilIdentidade >
                    <WrapperAvatarIdentidade>
                      <Avatar alt="Diego Fontes" src={account.user.profile?.avatar} style={{
                                width: '80px',
                                height: '80px',
                      }} />
                    </WrapperAvatarIdentidade>
                    <WrapperPerfilInfoIdentidade >
                        <Typography variant="h6">
                            {account.user?.first_name}
                        </Typography>
                        <Typography variant="caption" color="textSecondary">
                          {account.user.profile?.profissao}
                        </Typography>
                    </WrapperPerfilInfoIdentidade>
              </WrapperPerfilIdentidade>
            </AccordionSummary>
          <AccordionDetails>    
            <List>
                <ListItem>
                  <ListItemAvatar>
                      <EmailIcon />
                  </ListItemAvatar>
                  <ListItemText primary="E-mail" secondary={account.user?.email} />
                </ListItem>

                <ListItem>
                  <ListItemAvatar>
                      <PhoneIcon />
                  </ListItemAvatar>
                  <ListItemText primary="Telefone" secondary={account.user.profile?.telefone} />
                </ListItem>

                <ListItem>
                  <ListItemAvatar>
                      <RoomIcon />
                  </ListItemAvatar>
                  <ListItemText primary="Endereço" secondary={account.user.profile?.endereco} />
                </ListItem>
            </List>
          </AccordionDetails>
        </Accordion>
      </>       
       :
      <></>
    }

        <CardContent >
          {
            account.servico ?
              <Typography variant="caption" gutterBottom style={{margin: '0 10px 0 0'}} onClick={ () => setTab(0) } >
                  SERVIÇOS
              </Typography>
            :
            <></>
          }
          {
            account.bio ?
              <Typography variant="caption" gutterBottom style={{margin: '0 10px 0 0'}} onClick={ () => setTab(1) } >
                  BIOGRAFIA
              </Typography>
            :
            <></>              
          }
          {
            account.galeria ?
              <Typography variant="caption" gutterBottom style={{margin: '0 10px 0 0'}} onClick={ () => setTab(2) } >
                  GALERIA
              </Typography>
            :
            <></>
          }
        </CardContent>

        <TabComponet value={tab} setValue={setTab} componetList={[
            <TabServico account={account} match={match} />,  
            <TabBio account={account} match={match} />,
            <Galeria account={account} match={match} />,
          ]} 
        />
    
        </Main>
    </Container>
        :
        <></>
    }
    </>
  );
}

export { Home };
