import React from 'react';

import styled from 'styled-components';
import Modal from '@material-ui/core/Modal';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import Box from '@material-ui/core/Box';

import Button from '@material-ui/core/Button';

import CloseIcon from '@material-ui/icons/Close';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <div
          style={{
            width: '70vw',
            height: '80vh',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          {children}
        </div>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};


const ViewImagem = styled.div`
    width: 100vw;
    height: 100vh;
    position: relative;
    display: flex;

    align-items: center;
    justify-content: center;

    img {
        max-width: 90%;
        max-height: 90%;
        object-fit: cover;
    }

    .modal-sair{
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .seta-esquerda{
        position: absolute;
        right: 1px;
    }

    .seta-direita {
        position: absolute;
        left: 16px;
    }
`

export const useStyles = makeStyles((theme) => ({
    modal: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        // justifyContent: 'space-around',
        overflow: 'hidden',
        // backgroundColor: theme.palette.background.paper,
      },
      gridList: {
      },
      icon: {
        color: 'rgba(255, 255, 255, 0.54)',
      },
}));


const Carousel = ({carousel, close, imagesList, setIndexCarousel}) => {

    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const classes = useStyles();

    const handleChange = (newValue) => {
      setValue(newValue);
    };
  
    const handleChangeIndex = (index) => {
      setValue(index);
    };
  
    const [openView, setOpenView] = React.useState(false);

    const handleCloseView = () => {
        setOpenView(false);
    };

    React.useEffect(() => {
        console.log(imagesList)
    },[imagesList])

    return (
        <Modal
                open={carousel.status}
                onClose={close}
                className={classes.modal}
                aria-labelledby="simple-modal-galeria"
                aria-describedby="simple-modal-galeria"
            >
            <ViewImagem 
            >
                <div  className="modal-sair" >
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            
                            onClick={close}
                            edge="start"
                        >
                            <CloseIcon style={{color: '#ffffff'}} />
                        </IconButton>
                </div>
                <div  className="seta-esquerda" >
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            
                            onClick={ () => carousel.index + 1 === imagesList.length ? setIndexCarousel(0) :  setIndexCarousel( (carousel.index + 1) )}
                            edge="start"
                        >
                            <ArrowForwardIosIcon style={{color: '#ffffff'}} />
                        </IconButton>
                </div>
                <SwipeableViews
                        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                        index={carousel.index}
                        onChangeIndex={handleChangeIndex}
                    >
                    {
                        imagesList.map( (image, index) => (
                            <TabPanel value={carousel.index} index={index} dir={theme.direction}>
                                     <img src={`${image.img}`} alt=""  />
                            </TabPanel>
                        ))
                    }                  
                </SwipeableViews>
                <div  className="seta-direita" >
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            onClick={ () => carousel.index === 0 ? setIndexCarousel((imagesList.length - 1)) :  setIndexCarousel( (carousel.index - 1) )}
                            edge="start"
                        >
                            <ArrowBackIosIcon style={{color: '#ffffff'}} />
                        </IconButton>
                </div>
            </ViewImagem>
        </Modal>
    );
}

export  { Carousel };
