import React from 'react';


import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

import {
    WrapperCardMedia,
    WrapperServico,
    WrapperGrupoServico,
    CardServico,
    useStyles 
} from './styles';

import {
    Link
  } from "react-router-dom";

const TabServico = ({account, match}) => {
    
    const classes = useStyles();

    return (
        <WrapperServico>
            {
            account.user?.categoria_servico.map( (categoria_servico, index) => (
                    <WrapperGrupoServico key={index} >
                        <Typography variant="overline" gutterBottom>
                            {categoria_servico.name}
                        </Typography>
                        {categoria_servico.servico.map( (servico, index) => (
                            <Link  key={index}
                            to={{
                                pathname: `${match?.params.a}/s`,
                                // search: "?sort=name",
                                // hash: "#the-hash",
                                state: { 
                                servico: servico,
                                categoria: categoria_servico.name,
                                cartao: account.user.cartao?.cor,
                                }                                
                            }}>
                                <CardServico>
                                    <div>
                                    <CardContent >
                                        <Typography variant="h6">
                                            {servico?.nome}
                                        </Typography>
                                        <Typography variant="body2" color="textSecondary">
                                            {servico?.descricao}
                                        </Typography>
                                    </CardContent>
                                    </div>
                                    <WrapperCardMedia>
                                    <CardMedia
                                        className={classes.cover}
                                        image={servico?.img}
                                        title={servico?.descricao}
                                    />
                                    </WrapperCardMedia>
                            </CardServico>
                            </Link>
                        ))}
                </WrapperGrupoServico>
            ))
            }
        </WrapperServico>
    );
}

export  { TabServico };
