const LISTADEMSG = {
    nome: { 
            sucesso: "", 
            erro: "Nome não é valido"
        },
    email: { 
            sucesso: "", 
            erro: "E-mail não é valido"
        },
    senha: { 
            sucesso: "", 
            erro: "Senha não é valida"
        },
    senhaConfirma: {
        sucesso: "", 
        erro: "Senha não coincide"        
    },
    telefone: {
        sucesso: "", 
        erro: "telefone não é valido"        
    },
    descricao: {
        sucesso: "", 
        erro: "Descrição fornecida não é valida"        
    },
    nickname: {
        sucesso: "", 
        erro: "Nickname fornecida não é valido"        
    }
}

function text(value, msg=false, required=false){
    if (value){
        value = value.replace(/  +/g, ' ');
        if(value.length > 2 ){
            return {value: value, status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : "" }
        }else{
            return {value: value, status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : "" }
        }
    }
    return required ? {value: value, status: false, msg: "" } : {value: value, status: true, msg: "" }
}

function email(value, msg=false, required=false){
    let mailformat = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    if (value){
        value = value.replace(/  +/g, ' ');
        if( value.match(mailformat) ){
            return {value: value, status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        }
    }
    return required ? {value: value, status: false, msg: "" } : {value: value, status: true, msg: "" }
}

function senha(value, msg=false, required=false){
    
    //A string deve conter pelo menos 1 caractere alfabético minúsculo
    //A string deve conter pelo menos 1 caractere alfabético maiúsculo
    //A string deve conter pelo menos 1 caractere numérico
    //A string deve conter pelo menos um caractere especial,
    //A string deve ter oito caracteres ou mais
    if(value){
        var strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})");
        var mediumRegex = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{6,})");
    
        if(strongRegex.test(value)) {
            return {value: value, status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        } else if(mediumRegex.test(value)) {
            return {value: value, status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        } else {
            return {value: value, status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : "" }
        }
    }
    return {value: value, status: true, msg: ""}

}

function senhaConfirma(senha, value, msg=false){
    
    if(value){
        if(senha === value){
            return {value: value, status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        }else{
            return {value: value, status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : ""}
        }
    }
    return {value: value, status: true, msg: ""}

}


function telefone(value, msg=false, required=false){
    if(value){
        value = value.split("").filter(n => (Number(n) || n === 0)).join("").replace(" ", "")
        if( value.length > 9 && value.length < 12 ){
            return {value: value, status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
        }else{
            return {value: value, status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : ""}
        }
    }
    return required ? {value: value, status: false, msg: "" } : {value: value, status: true, msg: "" }
}


function nickname(value, msg=false, required=false){
        let nicknameRegex = new RegExp("^(?=[a-zA-Z0-9._]{5,30}$)(?!.*[_.]{2})[^_.].*[^_.]$");
        if(value){
            if(nicknameRegex.test(value)) {
                return {value: value, status: true, msg: msg ? LISTADEMSG[msg]?.sucesso ? LISTADEMSG[msg]?.sucesso: "" : ""}
            } else {
                return {value: value, status: false, msg: msg ? LISTADEMSG[msg]?.erro ? LISTADEMSG[msg]?.erro : "" : "" }
            }
        }

    return {value: value, status: true, msg: ""}
}

function formEhValido(form){
    let n = 0
    let v = 0
    for (var key in form) {
          n = n + 1
          v = form[key].valido ? v + 1 : v
    }
    return n === v
}

