import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import TextField from '@material-ui/core/TextField';

import InputMask from 'react-input-mask';

import {
    WrapperValidaForm,
    WrapperValidaInput,
} from './styles';

import { envia, atualiza } from '../../utils';

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});


function Validar({perfil, cor, status, open, close, concluir}) {
    
    const classes = useStyles();

    const [input, setInput] = React.useState(null);
    const [inputCode, setInputCode] = React.useState(null);
    const [code, setCode] = React.useState(null);

    const mudandoInput = (event) => {
        setInput(event.target.value)
    };

    const mudandoInputCode = (event) => {
        setInputCode(event.target.value)
    };

    const EnviarValidacao = async (event) => {
        let novoEnvio = {
            cliente: perfil.id,
            telefone: input.split("").filter(n => (Number(n) || n == 0)).join("").replace(" ", ""),
        }
        let result = await envia('profile_client_validade', novoEnvio)
        if (result?.id){
            setCode(result);
        }
    };

    const EnviarValidacaoCode = async (event) => {
        let novoEnvio = {
            code: inputCode,
        }
        let result = await atualiza('profile_client_validade', novoEnvio, `${code.id}/`)
        if (result?.id){
            close();
            setTimeout(() => {
                concluir()
            }, 300);
        }
    };


  return (
    <div>
      <Dialog fullScreen open={status} onClose={close} TransitionComponent={Transition}>
        <AppBar className={classes.appBar} style={{ background: cor }}  >
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={close} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              
            </Typography>
            {/* <Button autoFocus color="inherit" onClick={close}>
              save
            </Button> */}
          </Toolbar>
        </AppBar>
        <WrapperValidaForm>
            {
                code ? 
                <>
                    <WrapperValidaInput >
                                <TextField
                                    label="Codigo"
                                    style={{ margin: '5px 0 10px 0' }}
                                    placeholder=""
                                    helperText="999999"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    value={inputCode}
                                    onChange={mudandoInputCode}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />                     
                    </WrapperValidaInput>
                    <Button variant="contained" color="primary" disableElevation  onClick={EnviarValidacaoCode}  > VALIDAR </Button>
                </>        
                :
                <>
                    <WrapperValidaInput >
                        <InputMask mask="(99) 99999-9999" value={input} onChange={mudandoInput}>
                            {() => (
                                <TextField
                                    label="Telefone"
                                    style={{ margin: '5px 0 10px 0' }}
                                    placeholder=""
                                    helperText="(11) 99999-9999"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                /> 
                            )}
                        </InputMask>
                    </WrapperValidaInput>
                    <Button variant="contained" color="primary" disableElevation  onClick={EnviarValidacao}  > VERIFICAR </Button>
                </>        
            }
        </WrapperValidaForm>
      </Dialog>
    </div>
  );
}

export { Validar }