import React from "react";
import Helmet from "react-helmet";

import { URLSITE } from '../environment'

function Head({head}) {
    
    const [customHead, setCustomHead] = React.useState(false)

    React.useEffect( () => {
        setCustomHead(head)
    },[head])

    return (
        <>
            {
                customHead ?
                    <HeadCustom head={customHead} />
                :
                    <HeadInicial />
            }
        </>
    );

}

function HeadCustom ({head}) {
    
    return (
        <Helmet>
            {/* <!-- HTML Meta Tags --> */}
            <title>{ `${head?.account_name}`.toUpperCase() }</title>
            <meta
            name="description"
            content={` ${head.user?.first_name} - ${head.user?.profile.profissao} `}
            />

            {/* <!-- Google / Search Engine Tags --> */}
            <meta itemprop="name" content="Reed" />
            <meta
            itemprop="description"
            content={` ${head.user?.first_name} - ${head.user?.profile.profissao} `}
            />
            <meta
            itemprop="image"
            content={head.user?.cartao.logo}
            />

            {/* <!-- Facebook Meta Tags --> */}
            <meta property="og:url" content={`${URLSITE}/#/${head.account_name}`} />
            <meta property="og:type" content="website" />
            <meta property="og:title" content="Reed" />
            <meta
            property="og:description"
            content={` ${head.user?.first_name} - ${head.user?.profile.profissao} `}
            />
            <meta
            property="og:image"
            content={head.user?.cartao.logo}
            />

            {/* <!-- Twitter Meta Tags --> */}
            <meta name="twitter:card" content="summary_large_image" />
            <meta name="twitter:title" content="Reed" />
            <meta
            name="twitter:description"
            content={` ${head.user?.first_name} - ${head.user?.profile.profissao} `}
            />
            <meta
            name="twitter:image"
            content={head.user?.cartao.logo}
            />
        </Helmet>
    )
}

function HeadInicial () {
    
    return (
        <Helmet>
            {/* <!-- HTML Meta Tags --> */}
            <title>CARTÃO.DO</title>
            <meta
            name="description"
            content="Cartão de visita interativo."
            />

            {/* <!-- Google / Search Engine Tags --> */}
            <meta itemprop="name" content="Reed" />
            <meta
            itemprop="description"
            content="Cartão de visita interativo."
            />
            <meta
            itemprop="image"
            content="http://reedbarger.com/static/ca-styled-logo-5205dbb0983e1531d228c98584ab5711.png"
            />

            {/* <!-- Facebook Meta Tags --> */}
            <meta property="og:url" content="https://reedbarger.com" />
            <meta property="og:type" content="website" />
            <meta property="og:title" content="Reed" />
            <meta
            property="og:description"
            content="Cartão de visita interativo."
            />
            <meta
            property="og:image"
            content="http://reedbarger.com/static/ca-styled-logo-5205dbb0983e1531d228c98584ab5711.png"
            />

            {/* <!-- Twitter Meta Tags --> */}
            <meta name="twitter:card" content="summary_large_image" />
            <meta name="twitter:title" content="Reed" />
            <meta
            name="twitter:description"
            content="Cartão de visita interativo."
            />
            <meta
            name="twitter:image"
            content="http://reedbarger.com/static/ca-styled-logo-5205dbb0983e1531d228c98584ab5711.png"
            />
        </Helmet>
    )
}

export { Head }